<template>
  <div class="detail">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="form">
        <el-form-item label="校区" prop="campusId">
          <el-radio-group v-model="form.campusId">
            <el-radio :key="key" v-for="(item,key) in campus" :label="item.id" border>{{item.name}}</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="标题" prop="title">
          <el-input class="input" v-model="form.title" placeholder="请输入标题"></el-input>
        </el-form-item>
        <el-form-item label="正文内容" prop="content">
          <wang-editor v-if="!editStatus || form.content" :content.sync="form.content"></wang-editor>
        </el-form-item>
      </el-form>
      <div class="btns">
        <div @click="doSubmit" class="btn-submit mr20">{{editStatus? '更新': '确定'}}</div>
        <!--<div v-show="editStatus" class="btn-submit mr20">更新</div>-->
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
    </main-part>
  </div>

</template>
<script>
  export default {
    data() {
      // 验证正文内容
      var checkContent = (rule, value, callback)=> {
        if (!value || value === '<p><br></p>') {
          callback(new Error('请输入正文内容'));
        } else {
          callback();
        }
      }
      return {
        // 是否为编辑状态
        editStatus: this.$route.params.id !== '0',
        nav: [
          {name: "招生资讯", path: "go(-1)"},
          {name: this.editStatus ? "修改公告": "发布公告", path: ""}
        ],
        campus: [],
        form: {
          title: '',
          content: '',
          campusId: ''
        },
        rules: {
          campusId: { required: true, message: '请选择校区', trigger: 'change' },
          title: { required: true, message: '请输入标题', trigger: 'change' },
          content: { required: true, validator: checkContent, trigger: 'change' },
        }
      }
    },
    watch:{
      'form.content': function () {
        setTimeout(()=>{
          this.$refs.form.validate()
        },1)
      }
    },
    methods: {
      // 获取所有的校区
      async getCampusList() {
        this.campus = await this.$ajax({
          url: '/campus/queryCampusList.do'
        })
      },
      // 获取页面内容
      async getDetail(){
        this.form = await this.$ajax({
          url: '/rnotice/getNotice.do',
          query:{
            noticeId: this.$route.params.id
          }
        })
      },
      // 提交
      async doSubmit(){
        let text = '提交成功'
        let url = '/rnotice/addNotice.do'
        if(this.editStatus){
          text = '修改'
          url = '/rnotice/updateNotice.do'
          this.form.noticeId = this.form.id
        }
        await this.$refs.form.validate()
        await this.$ajax({
          url,
          el: '.btn-submit',
          query: this.form
        })
        this.$remind(text)
        this.$router.push('/recruit/information')
      }
    },
    mounted(){
      this.getCampusList()
      if(this.editStatus){
        this.getDetail()
      }
    }
  }
</script>
<style lang="sass" scoped>
  .form
    margin-top: 20px
    .input
      width: 460px
  .btns
    margin-left: 80px
</style>
